import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-toggle-section',
  styleUrl: 'orderToggleSection.css'
})
export class OrderToggleSection {
    @Prop() left: string;
    @Prop() right: string;
    @Prop() lValue: string;
    @Prop() rValue: string;

    @Prop() name: string;

    @Prop({mutable: true}) value: string = "1";

    render() {
        return <Host>
            <div class="toggles">
                <ks-order-toggle active={this.value == this.lValue} text={this.left} name={this.name} value={this.lValue}
                    changeHandler={(value)=>this.value = value}
                ></ks-order-toggle>
                <ks-order-toggle active={this.value == this.rValue} text={this.right} name={this.name} value={this.rValue}
                    changeHandler={(value)=>this.value = value}
                ></ks-order-toggle>
            </div>
            <br />

            <div hidden={this.value != this.lValue}>
                <slot name="left" />
            </div>
            
            <div hidden={this.value != this.rValue}>
                <slot name="right" />
            </div>
        </Host>
    }
}